import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router'
import PropertyApiList from '../../../../../Components/ApiList/PropertyApiList'
import ApiHeader from '../../../../../Components/ApiList/ApiHeader'
import BarLoader from '../../../../../Components/Common/BarLoader'
import { toast, ToastContainer } from 'react-toastify'
import axios from 'axios'
import { useRef } from "react";
import ReactToPrint from "react-to-print-advanced";
import SamReciept from "./SamReciept";
import BrandLoader from '../../../../../Components/Common/BrandLoader'
import ServerErrorCard from '../../../../AdvertisementModule/ServerErrorCard'
import CommonModal from '../../../../../Components/GlobalData/CommonModal'
import { AiFillPrinter } from 'react-icons/ai'

const SamPrintPage = () => {
  const componentRef = useRef();

  const id = useParams()

  const { api_samReciept } = PropertyApiList()

  const [loader, setloader] = useState(false)
  const [samDetails, setsamDetails] = useState()
  const [isLoading, setisLoading] = useState(false);
  const [erroState, seterroState] = useState(false);

  useEffect(() => {
    setisLoading(true)

    axios.post(api_samReciept, { memoId: id?.id }, ApiHeader())
      .then((res) => {
        console.log('getting sam memo details => ', res)
        setisLoading(false)
        setsamDetails(res?.data?.data)
      })
      .catch((err) => {
        console.log("getting sam memo error => ", err)
        setisLoading(false)
        seterroState(true)
      })
  }, [])

  if (isLoading) {
    return (
      <>
        <BrandLoader />
      </>
    )
  }
  if (erroState) {
    return (
      <CommonModal>
        <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
      </CommonModal>
    )
  }

  return (
    <div>




      <ReactToPrint
        trigger={() => <div className='z-10 fixed bottom-10 text-center  justify-center items-center  w-screen'>
        <button className="ml-4 font-bold px-6 py-1 bg-indigo-500 text-white  text-sm leading-tight uppercase rounded  hover:bg-indigo-700 hover:shadow-lg focus:shadow-lg focus:outline-none focus:ring-0 active:bg-indigo-800 active:shadow-lg transition duration-150 ease-in-out shadow-xl border border-white">
            <AiFillPrinter className='inline text-lg' />
            print
        </button>
    </div>}
        content={() => componentRef.current}
      />
      <SamReciept ref={componentRef} samDetails={samDetails} />
    </div>
  );
};

export default SamPrintPage;
